@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
.app__app {    
    width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.navbar {
  height: 130px;
  display: flex;
  align-items: flex-end;
  padding: 1rem;
  box-shadow: 0 0 5px gray;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.navbar-links {
  display: flex;
  align-items: flex-end;
}

.navbar-links * {
  text-decoration: none;
  color: black;
  margin-right: auto;
}

.navbar-links h1 {
  font-size: 3rem;
  font-style: cursive;
}

.navbar-links_logo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.navbar-links_logo img {
  height: 100px;
  width: 100px;
  border-radius: 50%;
}

.navbar-links_container {
  display: flex;
  padding: 10px;
  margin-left: 40px;
}

.navbar-links_container a {
  margin: 0 16px;
}

.navbar-links_container input {
  border-radius: 5px;
  padding: 2px 15px;
  outline: none;
  border: none;
  color: black;
}

.navbar-links_container p {
  font-size: 20px;
  text-decoration: underline;
}

.navbar-sign {
  margin-left: auto;
}

.navbar button {
  background-color: rgb(255, 0, 157);
  margin: 5px 15px;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  outline: none;
  border: solid 1px gray;
}

.nfts {
  padding: 25px;
}

.nfts-container_cards {
  display: flex;
  flex-wrap: wrap;
  margin: 16px;
  min-height: 80vh;
}

.nfts-container_text {
  padding-left: 50px;
}

.nfts-container_text h1 {
  font-size: 40px;
}

.nft-card {
  height: 450px;
  width: 350px;
  margin: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 0 5px rgb(209, 209, 209);
  border-radius: 4px;
  position: relative;
}

.nft-card a {
  text-decoration: none;
  background-color: rgb(252, 199, 208);
  box-shadow: 0 0 3px rgb(153, 147, 147);
  border-radius: 5px;
  color: black;
  padding: 5px 10px;
  position: absolute;
}

.nft-card img {
  height: 100%;
  width: 100%;
  border-radius: 4px;
}

.nft_details {
  width: 50vw;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gift-token {
  width: 100%;
  display: flex;
  padding: 10px 0 20px 50px;
  margin-bottom: 50px;
}

.gift-token input {
  padding: 7px 10px;
  color: black;
  border-radius: 5px;
  border: 1px solid rgba(252, 51, 202, 0.452);
  outline: none;
  width: 70%;
  margin-right: 30px;
  letter-spacing: 2px;
  font-family: monospace;
}

.gift-token span {
  border: 1px solid rgba(252, 87, 210, 0.452);
  width: 120px;
  height: 40px;
  border-radius: 5px;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  margin-left: 5px;
  cursor: pointer;
}

.token-img {
  width: 700px;
  height: 700px;
  box-shadow: 0 0 2px rgba(128, 128, 128, 0.87);
  border-radius: 20px;
  margin: 20px;
}

.token-img img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.token-details {
  padding: 20px 40px;
  width: 100%;
  text-align: left;
}

.tkn-lbl {
  font-size: 20px;
  font-weight: 500;
  text-decoration: underline;
  margin: 10px;
}

.tkn-det {
  margin: 10px;
}

.gft-sub {
  font-size: 30px;
  width: 100%;
  padding-left: 50px;
}

.create {
  min-height: 80vh;
}

.form-new {
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-new .form-form {
  border: 1px solid gray;
  width: 500px;
  padding: 25px;
  margin: 32px;
  border-radius: 10px;
}

.create_form {
  font-size: 30px;
  margin-top: 20px;
  margin-left: 30px;
}

.create-canvas {
  width: 100%;
  height: 400px;
  border-radius: 7px;
  margin-bottom: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.mint-btns {
  display: flex;
}

* {
  font-family: 'Inconsolata', monospace;  
}

